const screens = {
  WELCOME: 'WELCOME',
  ROUND_START: 'ROUND_START',
  CLICK_NOW: 'CLICK_NOW',
  SHOW_SCORE: 'SHOW_SCORE',
  TOO_SOON: 'TOO_SOON',
}
function startRound(setScreen, setTimeoutVar) {
  console.log('startRound');
  setScreen(screens.ROUND_START);
  var delay = Math.random() * 4 + 2;
  setTimeoutVar(setTimeout(() => setScreen(screens.CLICK_NOW), delay * 1000));

}
function Welcome(props) {
  // console.log(props);
  return (<Box mt={5} height={300} onClick={e => startRound(props.setScreen, props.setTimeoutVar)}>
    <Typography component={'span'}>
      <Box fontSize="h2.fontSize" m={1}>Reaction Time Test</Box>
      <Box fontSize="h6.fontSize" m={1}>When the screen turns green, click on it as quickly as you can.</Box>
      <Box fontSize="h6.fontSize" fontWeight="fontWeightBold" m={1} >Click anywhere to start...</Box>
    </Typography>

  </Box>);
}

function restart(setRound, setScreen, setScoreSum, setTimeoutVar) {
  setRound(0);
  setScoreSum(0);
  startRound(setScreen, setTimeoutVar);

}
function RoundStart(props) {

  return (
    <Box height={300} display="flex" flexDirection="column" alignItems="center" justifyContent='center' p={3} onClick={function (e) {
      clearTimeout(props.timeoutVar);
      props.setScreen(screens.TOO_SOON);

    }}>
      {/* style={{ backgroundColor: '#ff3d00' }} */}
      {/* <MoreHorizIcon fontSize={'large'} style={{ 'color': 'white' }} ></MoreHorizIcon>
      <Typography variant='h2' style={{ 'color': 'white' }} >Wait for green </Typography> */}
      <MoreHorizIcon fontSize={'large'}  ></MoreHorizIcon>
      <Typography variant='h2' >Wait for green </Typography>
    </Box>
  );
}
function ClickNow(props) {
  var time_start = performance.now();
  return (
    <Box height={300} display="flex" flexDirection="column" alignItems="center" justifyContent='center' p={3} style={{ backgroundColor: '#00a152' }} onClick={function (e) {
      var reactionTime = performance.now() - time_start;
      props.setReactionTime(reactionTime);
      props.setScoreSum(reactionTime + props.scoreSum);
      if (props.round >= props.rounds - 1) {
        props.setScreen(screens.FINISH);
      } else {
        props.setRound(props.round + 1);
        startRound(props.setScreen, props.setTimeoutVar);
      }
    }}>
      <Typography variant='h2' style={{ 'color': 'white' }} >Click! </Typography>
    </Box>
  );
}
function ShowScore(props) {
  return <h1>Scores</h1>;
}
function TooSoon(props) {
  return (
    <Box height={300} display="flex" flexDirection="column" alignItems="center" justifyContent='center' p={3} onClick={function (e) { startRound(props.setScreen, props.setTimeoutVar); }}>
      <Typography component={'span'}>
        <Box fontSize="h2.fontSize">
          Too soon!
      </Box>
        <Box fontSize="h5.fontSize">
          Click to try again.
      </Box>
      </Typography>
    </Box>);
}
