function Ad(props) {

  let style = {
    width: '100%',
    height: '600px'
  };
  if(props.approved==1) { 
    return (
      <iframe
        title="Amazon Products"
        style={style}
        marginWidth="0"
        marginHeight="0"
        scrolling="no"
        frameBorder="0"
        src="ad.html"
      />)
  } else { 
    return null;
  }
}
function requestNotify(approved, setApproved) {
  requestNotification(approved, setApproved);
}
function Score(props) {
  if (props.approved==1) {
    return (
      <Typography variant='h5'><Box fontWeight="fontWeightBold">{parseInt(props.average)}ms</Box></Typography>
    )
  } else {
    return <>
      <Button variant="contained" color="primary" startIcon={<VisibilityIcon />} onClick={e => requestNotify(props.approved, props.setApproved)}>
        My score
    </Button>
    </>
  }
}
function Render(props) {
  // props.setScreen(screens.Welcome);
  // console.log(props.setScreen);
  switch (props.screen) {
    case screens.WELCOME:
      return <Welcome setScreen={props.setScreen} setTimeoutVar={props.setTimeoutVar} />
    case screens.ROUND_START:
      return <RoundStart setScreen={props.setScreen} timeoutVar={props.timeoutVar} />
    case screens.CLICK_NOW:
      return <ClickNow
        setReactionTime={props.setReactionTime}
        setScreen={props.setScreen}
        setTimeoutVar={props.setTimeoutVar}
        round={props.round}
        setRound={props.setRound} rounds={props.rounds}
        scoreSum={props.scoreSum} setScoreSum={props.setScoreSum} />
    case screens.SHOW_SCORE:
      return <ShowScore />
    case screens.TOO_SOON:
      return <TooSoon setScreen={props.setScreen} setTimeoutVar={props.setTimeoutVar} />
    case screens.FINISH:
      return <Finish setApproved={props.setApproved} approved={props.approved} rounds={props.rounds} scoreSum={props.scoreSum} score={props.score} setScreen={props.setScreen} setRound={props.setRound} setTimeoutVar={props.setTimeoutVar} setScoreSum={props.setScoreSum}></Finish>
    default:
      return <div>Empty</div>
  }
}
