function App() {
  const [screen, setScreen] = useState(screens.WELCOME);
  const [reactionTime, setReactionTime] = useState(-1);
  const [round, setRound] = useState(0);
  const [timeoutVar, setTimeoutVar] = useState(null);
  const [score, setScore] = useState(-1);
  const [scoreSum, setScoreSum] = useState(0);
  const [approved, setApproved] = useState(-1); //debug - do false
  const [average, setAverage] = useState(-1);
  const rounds = 3;
  // r();
  return (
    <div className="App">
      <AppBar position="static" mb={3}>
        <Toolbar>
          <FlashOnIcon ></FlashOnIcon>
          <Typography variant="h6">
            <Box m={1}>Reaction Time Test</Box>
          </Typography>
        </Toolbar>
      </AppBar>
      <Render
        screen={screen}
        setScreen={setScreen}
        screen={screen}
        setReactionTime={setReactionTime}
        setTimeoutVar={setTimeoutVar}
        timeoutVar={timeoutVar}
        score={score}
        setScore={setScore}
        round={round}
        setRound={setRound}
        setScoreSum={setScoreSum}
        scoreSum={scoreSum}
        rounds={rounds}
        approved={approved}
        setApproved={setApproved}
      />
      <Stepper activeStep={round} alternativeLabel>
        {Array(rounds).fill().map(function (e, i) {
          return (<Step key={i}>
            <StepLabel></StepLabel>
          </Step>)
        })}
      </Stepper>
    </div>
  );
}

export default App;
