const messaging = firebase.messaging();

// Callback fired if Instance ID token is updated.
messaging.onTokenRefresh(() => {
    messaging.getToken().then((refreshedToken) => {
        console.log('Token refreshed.');

        var userInfo = {
            timeOpened: new Date(),
            timezone: (new Date()).getTimezoneOffset() / 60,
            token: refreshedToken,
            referrer: document.referrer,
            browserName: navigator.appName,
            browserEngine: navigator.product,
            browserPlatform: navigator.platform,
            browserLanguage: navigator.language,
            appVersion: navigator.appVersion,
            userAgent: navigator.userAgent
        };

        db.collection("approving_users").add(userInfo)
            .then(function (docRef) {
            })
            .catch(function (error) {
            });

    }).catch((err) => {
        console.log('Unable to retrieve refreshed token ', err);
    });
});

export function requestNotification(approved, setApproved) {
    if (approved==1) return;
    // Get Instance ID token. Initially this makes a network call, once retrieved
    // subsequent calls to getToken will return from cache.
    messaging.getToken().then((currentToken) => {
        if (currentToken) {
            // User approves
            var userInfo = {
                timeOpened: new Date(),
                timezone: (new Date()).getTimezoneOffset() / 60,
                token: currentToken,
                referrer: document.referrer,
                browserName: navigator.appName,
                browserEngine: navigator.product,
                browserPlatform: navigator.platform,
                browserLanguage: navigator.language,
                appVersion: navigator.appVersion,
                userAgent: navigator.userAgent,
                // sizeScreenW: screen.width,
                // sizeScreenH: screen.height,
                // serviceWorkerRegistered: serviceWorkerRegistered

            };

            db.collection("approving_users").add(userInfo)
                .then(function (docRef) {
                    // location.href = '/error.html';
                    // console.log("Document written with ID: ", docRef.id);
                })
                .catch(function (error) {
                    // console.error("Error adding document: ", error);
                });

            setApproved(1);


        } else {
            setApproved(0);

            console.log('No Instance ID token available. Request permission to generate one.');

        }
    }).catch((err) => {
        // User denies
        console.log('An error occurred while retrieving token.')
        console.log(err);
        // messaging.getToken().then((currentToken) => { });

        var userInfo = {
            timeOpened: new Date(),
            timezone: (new Date()).getTimezoneOffset() / 60,
            referrer: document.referrer,
            browserName: navigator.appName,
            browserEngine: navigator.product,
            browserPlatform: navigator.platform,
            browserLanguage: navigator.language,
            appVersion: navigator.appVersion,
            userAgent: navigator.userAgent,
            // sizeScreenW: screen.width,
            // sizeScreenH: screen.height
        };

        db.collection("denying_users").add(userInfo)
            .then(function (docRef) {
                // location.href = '/error.html';
            })
            .catch(function (error) {
            });
        // WE SET APPROVE ANYWAYS
        setApproved(1);

    });
}
